.episode-list {
  display: flex;
  flex-wrap: wrap;
}
.episode-list li{
  width: 25%;
  height: 250px;
}
.badge.badge-primary{
  padding: 10px;
}
#root{
  position: relative;
  max-width: 1200px;
  margin: auto;
}
.wrap{
  position: relative;
  max-width: 1200px;
  width: 100%;
}

.search{
  position:fixed;
  top: 0;
  width:inherit;
  max-width:inherit;
  margin: auto;
  z-index:5;
  background: #fff;
}

.search-list{
  position: relative;
  z-index: 5;
  width: 100%;
  height: 100vh;
  background: #fff;
}
.search__close{
  display: flex;
  width: 100px;
  justify-content: center;
  align-items: center;
}
.search__close:hover{
  color:#0175ff;
  cursor: pointer;
}
.filter-item label{
  margin-left: 10px;
}
.search ul{
  padding: 0;
}
.search ul li{
  list-style: none;
}
.search ul li a{
  display: block;
}
input.form-control{ 
  transition: 0.3s;
}
.content{
  margin-top:80px;
}
.watch__iframe{
  margin: auto;
}
.content-episode{
  width:100px;
}

.switch-episode{
  margin-top: 20px;
}

@media screen and (max-width: 400px) {
  .breadcrumb-item{
    overflow: hidden;
    width: 100%;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .breadcrumb-item::before,.breadcrumb-item::after{
    display: none!important;
  }
 }